import { useState } from "react";

const Button = ({ value }) => {
  return (
    <div>
      <style jsx>{`
        div {
          border: 1px solid grey;
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: center;
          font-size: 4.5rem;
        }
      `}</style>
      {value}
    </div>
  );
};

const Calculator = () => {
  const [display, setDisplay] = useState("0");
  const [workingNum, setWorkingNum] = useState(0);

  const handleClick = value => {

    typeof value === "number" ? 
        display === "0" ? setDisplay(value.toString()) : setDisplay(display + value.toString()) 
    : console.log("hello")
        // : switch(value) {
    //     case "X": 
    //     setDisplay("Hahah");
    //     default: 
    //     console.log("what");
    // }

  };

  const symbolsAndNumbers = [
    "C",
    "()",
    "%",
    "/",
    7,
    8,
    9,
    "X",
    4,
    5,
    6,
    "-",
    1,
    2,
    3,
    "+",
    "+/-",
    "0",
    ".",
    "="
  ];
  return (
    <>
      <h2>Calculator</h2>

      <div id="container">
        <div id="display">{display}</div>
        {symbolsAndNumbers.map(value => {
          return (
            <div onClick={() => handleClick(value)}>
              <Button value={value} />
            </div>
          );
        })}
      </div>

      <style jsx>{`
        #container {
          box-sizing: border-box;
          max-width: 600px;
          height: 750px;
          margin: 0 auto;
          border: 1px solid black;
          display: grid;
          grid-template-columns: repeat(4, 1fr);
          grid-template-rows: repeat(7, 1fr);
        }
        #display {
          width: 100%;
          grid-column: 1 / 5;
          grid-row: 1 / 3;
          background-color: red;
          display: flex;
          justify-content: flex-end;
          align-items: center;
          font-size: 5rem;
        }
        #top-col,
        #sec-col,
        #thi-col,
        #fou-col,
        #fiv-col,
        #bot-col {
          grid-column: 1 / 5;
          display: flex;
          width: 100%;
          justify-content: space-evenly;
          align-items: center;
          font-size: 4.5rem;
        }
      `}</style>
    </>
  );
};

export default Calculator;
